import { Button, Paper, Stack, TextField } from "@mui/material";
import { styled } from "@mui/material/styles";
import React from "react";
import logo from "../assets/pics/logo.png";
import { useFormik } from "formik";

export default function LoginForm({ Loggin }) {
  const formik = useFormik({
    initialValues: {
      Username: "",
      Password: "",
    },

    onSubmit: (values) => {
      Loggin();
    },
  });
  return (
    <div style={classes.root}>
      <Paper sx={classes.LoginWrapper}>
        <img src={logo} alt="logo" style={classes.logo} />
        <form onSubmit={formik.handleSubmit} style={{ width: "100%" }}>
          <Stack
            direction="column"
            spacing={3}
            alignItems="center"
            sx={{ width: "100%" }}
          >
            <LoginStyledField
              label="Username"
              variant="outlined"
              fullWidth
              name="Username"
              onChange={formik.handleChange}
              value={formik.values.Username}
            />
            <LoginStyledField
              label="Password"
              variant="outlined"
              fullWidth
              name="Password"
              onChange={formik.handleChange}
              value={formik.values.Password}
            />
            <Button
              sx={classes.LoginButton}
              variant="contained"
              fullWidth
              type="submit"
              disabled={
                formik.values.Username === "" || formik.values.Password === ""
              }
            >
              Login
            </Button>
          </Stack>
        </form>
      </Paper>
    </div>
  );
}

const LoginStyledField = styled(TextField)({
  maxWidth: "400px",
  "& .MuiOutlinedInput-root": {
    "& fieldset": {
      borderRadius: "10px",
    },
  },
});

const classes = {
  root: {
    minHeight: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#EDF2F6",
  },
  LoginWrapper: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    p: { xs: "1rem", md: "2rem" },
    boxShadow: "rgba(17, 17, 26, 0.1) 0px 0px 16px",
    borderRadius: { xs: "0", md: "10px" },
    width: { xs: "100%", md: "auto" },
    minHeight: { xs: "calc(100vh - 2rem)", md: "500px" },
  },
  loginLabel: {
    fontFamily: "'Roboto', sans-serif",
    fontWeight: "600",
  },
  logo: {
    width: "clamp(6rem, 50%,15rem)",
    paddingBottom: "4em",
  },
  LoginButton: {
    maxWidth: "200px",
    height: "40px",
    borderRadius: "10px",
  },
};
